import { _decorator, Component, Node,sys, director, SceneAsset } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Instrucciones')
export class Instrucciones extends Component {

    @property(Node)
    public PC : Node = null;
    @property(Node)
    public mobile : Node = null;
    @property([Node])
    public lista: Node[] =[];
    @property([Node])
    public listaFinal: Node[] =[];
    @property([Node])
    public botones: Node[]=[];
    public index: number = 0;
 
 
    onLoad() {
        // Verifica si el juego se est ejecutando en un dispositivo mvil
        if (sys.isMobile) {
 
            var progress = 0;
            director.preloadScene(this.mobile.toString(), (completedCount: number, totalCount: number, item: any) => {
            progress = completedCount / totalCount;
            }, (error: Error, asset: SceneAsset) => {
                //do something after preloaded scene
            director.loadScene(this.mobile.toString());
            });
            // Cdigo especfico para dispositivos mviles
            // this.PC.active=false;
           this.mobile.active=true;
           this.listaFinal[0]=this.mobile;
        } else {
            // Cdigo para otras plataformas (por ejemplo, navegadores)
            this.PC.active=true;
           //this.mobile.active=false;
 
           this.listaFinal[0]=this.PC;
           var progress = 0;
            director.preloadScene(this.PC.toString(), (completedCount: number, totalCount: number, item: any) => {
            progress = completedCount / totalCount;
             }, (error: Error, asset: SceneAsset) => {
             //do something after preloaded scene
            director.loadScene(this.PC.toString());
            });
        }
 
        this.listaFinal[1]=this.lista[0];
        
    }
 
   
 
 
    start() {
 
        this.botones[1].active=false;
 
    }
    update (){
 
        if(this.index==0)
        {
            this.botones[1].active=false;
        }else
        {
            this.botones[1].active=true;
        }
 
        if(this.index==this.listaFinal.length-1)
        {
            this.botones[0].active=false;
        }
        else
        {
            this.botones[0].active=true;
        }
        
    }
 
    next()
    {
        if(this.index<this.listaFinal.length-1)
        {
            this.index++;
 
            this.listaFinal[this.index].active=true;
            this.listaFinal[this.index-1].active=false;
           
           
        }
       
    }
 
    back()
    {
        if(this.index>0)
        {
            this.index--;
            this.listaFinal[this.index].active=true;
            this.listaFinal[this.index+1].active=false;
           
        }
 
    }
 
}


